  const titleElement = document.getElementById('navigation');
  const languageSelect = document.getElementById('language-select');
  const menuLinks = document.querySelectorAll('.nav-link');
  const translatableElements = document.querySelectorAll('[id]');

  function updateText(language) {
    translatableElements.forEach(element => {
      const key = element.id;
      if (translations[language] && translations[language][key]) {
        element.textContent = translations[language][key];
    }
    });
    if (languageSelect) {
      languageSelect.value = language;
  }
  }
  function setLanguage(lang) {
    localStorage.setItem("selectedLanguage", lang); // Зберігаємо мову
    updateText(lang);
}



languageSelect.addEventListener('change', () => {
  const selectedLanguage = languageSelect.value;
  setLanguage(selectedLanguage);
});

// Встановлюємо збережену мову або за замовчуванням "ua"
document.addEventListener("DOMContentLoaded", function () {
  const savedLanguage = localStorage.getItem("selectedLanguage") || "ua";
  updateText(savedLanguage);
});
  